import React from "react";
import { NavLink } from "react-router-dom";
import "../../style/css/redSection.css";

const RedSection = () => {
	return (
		<div className="red-section-container">
			<h2>Nadal nie uzyskae odpowiedzi?</h2>
			<span>
				Jestemy otwarci na Pastwa pytania - chtnie rozwiejemy wszelkie
				wtpliwoci zwizane z wykonywaniem tumaczew naszym biurze. Zachcamy
				do skontaktowania si z nami poprzez formularz kontaktowy, do ktrego
				prowadzi poniszy przycisk.
			</span>
			<NavLink to="/contact" className="nav-btn-desktop">
				<button className="write-us-btn">Napisz do nas</button>
			</NavLink>
		</div>
	);
};

export default RedSection;
